$web-primary: #AA0601;

/*顏色設定*/ 
$font-primary-color:#650300;
$font-secondary-color:#fff;
$font-paragraph:#000;
$font-hover:#ffb100;

/*文字設定*/ 
$font-size-base:1rem; //16px
$h1-font-size: $font-size-base*4; //64px
$h2-font-size: $font-size-base*3; //48px
$h3-font-size: $font-size-base*2.25; //36px
$h4-font-size: $font-size-base*1.75; //28px
$h5-font-size: $font-size-base*1.5; //24px
$h6-font-size: $font-size-base*1.25; //20px

$font-primary-weight:bold;

/*字體設定*/ 
$font-noto: 'Noto Sans TC', sans-serif;
$font-roboto: 'Roboto', sans-serif;
$font-icon:'Material Icons Outlined';



/*匯入設定*/ 
@import "reset.css";


/*mixin設定*/ 
@mixin hide-text{
  text-indent:101%;
  white-space: nowrap;
  overflow: hidden;
}

/*RWD設定*/ 
body{
  font-family: $font-noto;
}
img{
  max-width: 100%;}

.container{
  width: 1110px;
  margin: 0 auto;
}



.header {
  background-color: $web-primary;
  color: $font-secondary-color;
  font-size: $h6-font-size;
  display: flex;
  justify-content: space-between;
  padding: 30px 88px;
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
     li a{
      padding-right: 64px;
      padding-top: 8px;
      padding-bottom: 8px;
      text-decoration: none;
      color: $font-secondary-color;
      &:hover{
        color: $font-hover;
        transition: all 0.8s;
      }
    }  
}
}
.menu li:last-child a{
  padding-right: 0;
}

.logo {
  width: 80px;
  height:49px;

}

.banner{
  background: url(https://hungcurry.github.io/week03/assets/images/index/banner.png) no-repeat center;
  height: 840px;
  color: $font-primary-color;
  .container{
    display: flex;
    justify-content:center;
    flex-direction: column;
    text-align: right;
    height: 100%;
  }
  .text-sm{
    font-size: $h3-font-size;
    margin-bottom: 16px;
  }
  .text-lg{
    font-weight: $font-primary-weight;
    ;
    font-size: $h1-font-size;
  }
}

.major{
   h2{
     color: $font-primary-color;
     font-size: $h2-font-size ;
     font-weight: $font-primary-weight;
   }
}


.material-icons-outlined {
  font-family: $font-icon;
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
}






.footer {
  background-color: $web-primary;
  color: $font-secondary-color;
  
}



